___CSS_LOADER_EXPORT___.push([module.id, `/* PostList.css */

.postList {
    padding: 20px;
}

.postGrid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 20px;
}

.postItem {
    position: relative;
    overflow: hidden;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s;
}

.postItem:hover {
    transform: scale(1.03);
}

.postImageContainer {
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 100%; /* Aspect ratio 1:1 */
    overflow: hidden;
}

.postImage {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
    border-radius: 8px;
}

.postOverlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    color: #fff;
    opacity: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    transition: opacity 0.3s;
}

.postItem:hover .postOverlay {
    opacity: 1;
}

.postAuthor,
.postDescription {
    margin: 0;
    font-size: 14px;
    padding: 5px;
}

.postLink {
    text-decoration: none;
    color: inherit;
}

/* Optional: Error class */
.error {
    color: red;
    text-align: center;
}
